import * as Yup from 'yup';
import { nameRegexForFeedback } from '../regex';

export const nameSchema = Yup.object({
  name: Yup.string()
    .min(2, "–í–≤–µ–¥—ñ—Ç—å —Å–≤–æ—î —ñ–º'—è, –±—É–¥—å –ª–∞—Å–∫–∞")
    .max(30, "–í–≤–µ–¥—ñ—Ç—å —Å–≤–æ—î —ñ–º'—è, –±—É–¥—å –ª–∞—Å–∫–∞")
    .trim()
    .matches(nameRegexForFeedback, "–í–≤–µ–¥—ñ—Ç—å —Å–≤–æ—î —ñ–º'—è, –±—É–¥—å –ª–∞—Å–∫–∞")
    .required("–í–≤–µ–¥—ñ—Ç—å —Å–≤–æ—î —ñ–º'—è, –±—É–¥—å –ª–∞—Å–∫–∞"),
});


